<template>
    <div class="container">
      <van-nav-bar
        title="搜索"
        left-arrow
        @click-left="$router.go(-1)"
      />
      <form action="/">
        <van-search v-model="search"
          show-action
          placeholder="请输入搜索关键词"
          @search="onSearch">
          <template #action>
             <div @click="onSearch(search)" class="searchButton">搜索</div>
          </template>
        </van-search>
      </form>
      <div class="searchHead" v-if="history.length > 0">
        <div class="searchTitle">最近搜索</div>
        <div class="clear"><van-icon name="delete-o" @click="clear"/></div>
      </div>
        <div  class="historyBody">
          <div class="searchHistory" @click="onSearch(item)" v-for="item in history" :key="item">{{ item }}</div>
        </div>
    </div>
  </template>

<script>
import { getHistoryItem, setHistoryItem } from '@/utils/storage'

export default {
  name: 'searchIndex',
  data () {
    return {
      search: '',
      history: getHistoryItem()
    }
  },
  methods: {
    onSearch (val) {
      const index = this.history.indexOf(val)
      if (index !== -1) {
        this.history.splice(index, 1)
      }
      this.history.unshift(val)
      setHistoryItem(this.history)
      this.$router.push(`/searchlist?search=${val}`)
    },
    clear () {
      this.history = []
      setHistoryItem([])
    }
  }
}
</script>
  <style scoped>
      .searchTitle{
        display: flex;
        margin-left: 20px;
        font-size: small;
        color: gray;
        margin-top: 15px;
        justify-content: center;

      }
      .historyBody{
        display: flex;
        text-align: center;
        flex-wrap: wrap;
      }
      .searchHistory
      {
        border: 1px gray solid;
        padding: 10px;
        margin-left:15px;
        font-size: medium;
        margin-top: 20px;
        background-color: rgb(247, 247, 247);
         justify-content: center;
         border-radius: 10px;
         word-break: break-all;
        }
      .searchHead{
        display: flex;
        justify-content: space-between;
      }
      .clear{
        margin-top: 15px;
        margin-right: 15px;
      }
      .searchButton{
        border-radius: 3px;
        background-color: rgb(207, 34, 1);
        border: none;
        padding-left: 5px;
        padding-right: 5px;
      }
  </style>
